.conteudo_principal_ramens {
  width: 100%;
  height: auto;
  background-color: var(--preto);
}

.conteudo_principal_ramens-titulo {
  color: var(--preto);
  text-align: center;
  font-size: 5rem;
  font-family: var(--fonte-padrao);
  text-transform: uppercase;
  background-color: var(--branco-translucido);
  padding: 20px 0;
  font-weight: 400;
}

.conteudo_principal_ramens-container {
  max-width: 65vw;
  margin: 0 auto;
  padding: 30px;
  display: flex;
  overflow-x: hidden;
}

.conteudo_principal_ramens-container-caixa {
  max-width: 20.25vw;
  height: auto;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
  background-color: var(--vermelho-bg);
  border: 1px solid #000;
  flex-direction: column;
  flex: none;
  justify-content: space-around;
  margin: 0 1.125vw;
  transition: all .5s;
  display: flex;
}

.conteudo_principal_ramens-container-caixa:hover {
  box-shadow: 0px 0px 10px 4px var(--branco-translucido);
  z-index: 99;
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}

.conteudo_principal_ramens-container-caixa .ramens-caixa-titulo {
  width: 100%;
  box-sizing: border-box;
  font-size: 3.125vw;
  font-family: var(--fonte-padrao);
  color: var(--branco-translucido);
  text-shadow: 0px 0px 5px var(--preto);
  padding: 2px 5px;
  font-weight: 400;
  display: inline-block;
}

.conteudo_principal_ramens-container-caixa .ramens-caixa-imagem {
  height: auto;
  box-sizing: border-box;
  max-width: 100%;
  padding: 20px;
}

.conteudo_principal_ramens-container-caixa .ramens-caixa_preco {
  color: var(--branco);
  text-shadow: 0px 0px 10px var(--preto);
  font-family: Courier New, Courier, monospace;
  font-size: 3.25vw;
  font-weight: 800;
  display: inline-block;
  position: relative;
  bottom: 0;
}

.conteudo_principal_ramens-container + .scrollbar .botao-carousel.ativo {
  background-color: var(--branco);
  box-shadow: 0 0 5px 2px red;
}

@media screen and (max-width: 1024px) {
  .conteudo_principal_ramens {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }

  .conteudo_principal_ramens-titulo {
    -webkit-transform: scale(.7);
    transform: scale(.7);
  }
}

@media screen and (max-width: 768px) {
  .conteudo_principal_ramens-titulo {
    font-size: 3rem;
  }

  .conteudo_principal_ramens-container-caixa {
    max-width: 99%;
  }

  .conteudo_principal_ramens-container {
    max-width: 75%;
    padding: 30px 0;
  }

  .conteudo_principal_ramens-container-caixa .ramens-caixa-titulo {
    font-size: 2rem;
  }

  .conteudo_principal_ramens-container-caixa .ramens-caixa_preco {
    font-size: 1.25rem;
  }

  .conteudo_principal_ramens {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

